.project {
    display: -ms-flexbox;
    display: flex;
}

.project>* {
    width: 50%;
}

.project>*:nth-of-type(2) {
    padding: 20px 10px;
    margin: auto;
}

video {
    width: 100%;
}

.project h2 {
    color: #9dc4df;
}

@media only screen and (max-width: 600px) {
    .project {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .project>* {
        width: 100%;
    }
    .project>*:nth-of-type(1) {
        -ms-flex-order: 2;
            order: 2;
    }
    .project>*:nth-of-type(2) {
        -ms-flex-order: 1;
            order: 1;
    }
  }


